.Category{
}
.Category h2{
    margin-bottom: 1rem;
    font-weight: 400;
    font-size: 28px;
    /*
    color: #fff;
    display: inline-block;
    width: min-content;
    */
    color: #FFF;
    background-color: #4C2B36;
    padding:5px;  
    letter-spacing: 2.5px;  
}


.CategoryHeader{
    color: #000;
    margin-bottom: 1rem;
    padding:10px 2.5rem;
    border-radius: 20px 20px 0 0;
    background-color: #fff;
}
.CategoryPreFooter{
    color: #000;
    background-color: #fff;
    margin: 10px 0 1rem;
    font-size: 16px;
    padding: 8px 12px;
    border: 1px solid;
    text-align: left;
}
.CategoryFooter{
    color: #fff;
    margin: 10px 0 1rem;
    font-size: 12px;
    padding: 8px 12px;
    border: 1px solid;
    text-align: left;
    border-radius: 0 20px 0 0;
}

.PriceBtns{
    display: flex;
    margin-bottom: 1rem;
    align-items: center;
    color: #fff;
    border:1px solid;
    border-radius: 5px;
    background-color: #104547;
    padding: 8px 5px;
    justify-content: flex-end;
}

.PriceBtns p{
    margin-right:auto;
}

.PriceBtns button{
    padding: 10px 5px;
    outline: 0;
    font-size: 12px;
    transition: background 0.5s;
    border-radius: 4px;
}

.PriceBtns button:first-of-type{
    margin-right: 10px;
}

.TakeAwayBtn{
    border:1px solid #2B954A;
    /*margin: 1rem auto;*/
}
.TakeAwayBtnActive{
    background-color: #2B954A;
    border:1px solid #2B954A;
    color: #fff;
}

.ShopBtn{
    border:1px solid #931A1F;
}
.ShopBtnActive{
    background-color: #931A1F;
    border:1px solid #931A1F;
    color: #fff;

    /*margin: 1rem auto;*/
}

.Back{
    padding-bottom: 1rem;
    padding: 1rem 0 2rem;
}

.Back a{
    border: 1px solid #fff;
    padding: 0.5rem 1rem;
    background: #BC471B;
    color: #fff;
    font-size: 14px;
}

.Back span{
    font-size: 14px;
    position: relative;
    top: 2px;
}


.TopNavigation{
    position: fixed;
    top: 8px;
    left: 14px;
    
    z-index:100;
    
    
}

.TopNavigation button{
    background-color: #fff;
    border-radius: 5px;  
    margin-left: 0; 
    padding: 5px; 
    -webkit-box-shadow: 2px 5px 9px 2px rgba(66,66,66,0.56); 
    box-shadow: 2px 5px 9px 2px rgba(66,66,66,0.56);
}