body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

body{
  /*font-family: 'Roboto', sans-serif;*/
  /*background-color: var(--bgColor);*/
  background-color: #718873;
}

blockquote,body,div,h1,h2,h3,h4,h5,h6,ol,p,ul{
  margin:0;
  padding:0
}

img{
  width:100%;
  height:auto;
  display:block
}

a{
  text-decoration:none;
  color:inherit
}

ol,ul{
  list-style:none
}

.wrapper{
  max-width:450px;
  padding:0 20px;
  margin:0 auto
}

header{
  width: 100%;
  margin-bottom: 1rem;
  background-color: #141115;
  color: #fff;
}

header .logo{
  width: 150px;
  margin: 0 auto;
}

header h1{
  text-align: center;
  font-size: 28px;
  padding: 10px;
  font-weight: 300;
}

aside{
  font-size:10px;
  margin-bottom:1rem;
  color:#fff;
  margin-top: 1rem;
}
