.FooterWrapper{
    max-width: 450px;
    margin: 0 auto;
}

.Address{
    display: block;
    text-align: right;
    align-items: center;
    position: relative;
}

.Address address{
    font-style: normal;
    font-weight: 100;
    font-size: 16px;
}

.Address a{
    display: block;
    font-size: 18px;
    padding: 5px;
    border-radius: 5px;

}

.Footer address span{
    display: block;
}



.Footer h3{
    font-weight: 100;
}

.SocialWrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 20px;
}

.SocialWrap h3{
    width: 100%;
    position: relative;
    padding-bottom: 3px;
    padding-top: 10px;
}

.Social{
    display: flex;
    justify-content: space-around;
    width: 100px;
    margin: 0 auto;
}

.Social li{
    padding: 0.5rem;
}

.Social li a{
    transform: scale(0.5);
    display: block;
}
.PhonesWrapper{
    padding: 10px 20px;
    background-color: #F5B700;
    color: #000;
}

.PhonesWrapper h3{
    padding-top: 0;
    font-size: 20px;
}
.Phones{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 1rem;
}

.Phones > div{
    display: flex;
    flex-direction: column;
    font-size: 10px;
    line-height: 12px;
}
.Phones > div a{
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.65px;
}

.Developer{
    font-size: 12px;
    padding: 0.5rem 0;
    position: relative;
    font-weight: 100;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.Developer h6{
    margin-left: 1ch;
}

.Developer:before{
    content: "";
    width: 20%;
    height: 1px;
    background-color: #fff;
    left: 40%;
    top:0;
    position: absolute;
}

.Developer h6{
    font-size: 16px;
    font-weight: 100;

}